import React from 'react';
import { useHistory } from 'react-router';
import Button from '../../../components/ui/Button';
import iRoute from '../../../interface/route.interface';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    label: string,
    icon: string,
    completed?: boolean,
    className?: string,
    footer?: React.ReactNode,
    route?: iRoute
}

const LensProposalBuilderCard : React.FC<Props>  = ({
    label,
    icon,
    completed = false,
    className = '',
    footer,
    route
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-proposal-builder-card');
    const history = useHistory();


    // HOOKS

    // METHODS
    const classModifiers = () => {
        const classes = [];
        if(completed) classes.push('completed');
        if(footer) classes.push('with-footer');
        return classes
    }

    const gotoRoute = () => {
        if (!route)return;
        history.push(route.path);
    }

    return (
        <div className={B(classModifiers()) + ` ${className}`} onClick={gotoRoute}>
            <div className={E('info')}>
                <h2>{label}</h2>

                { completed && <span>Completed</span> }
            </div>
            <Button icon={icon} type="secondary"  circle={true}/>

            { footer && footer}
        </div>
    )
}

export default LensProposalBuilderCard;