import React, { useEffect, useState } from 'react';
import RouterLink from './ui/RouterLink';
import Icon from './ui/Icon';
import sidebarSlice from '../store/slices/sidebar.slice';
import { useDispatch } from 'react-redux';
import routes from '../constants/routes.constant';
import { useLocation } from 'react-router';
import useUtils from '../utils/hooks/useUtils';
import productsSlice from '../store/slices/products.slice';

interface Props {

}

const  NavBar : React.FC<Props>  = () =>  {
    // VARIABLES
    const dispatch = useDispatch();
    const location = useLocation();
    const [ cloneObject ] = useUtils();
    const [ activeRoute, setActiveRoute ] = useState(routes.home);

    // HOOKS
    useEffect( () => {
        setActiveRoute(getActiveRoute(location.pathname));
    }, [location]);

    // METHODS
    const closeNavOnly = () => {
        return ( 
            // activeRoute?.id === routes.legalNotices.id ||
            activeRoute?.id === routes.myCompanion.id
        )
    }

    const getActiveRoute = (path: string) => {
        const routeKeys = Object.keys(routes);
        const clonedRoutes = cloneObject<any>(routes);
        let activeRouteKey = routeKeys.filter( (k: string) => clonedRoutes[k].path === path)[0];
        return clonedRoutes[activeRouteKey];
    }
    
    const openProducts = () => {
        dispatch(productsSlice.actions.open(true));
    }

    return (
        <div className="navbar">

            <RouterLink route={routes.home} className="navbar__logo">
                <Icon name="logo" type="image"/>
            </RouterLink>

            <h2 className="navbar__title h2">
                {activeRoute?.label}
            </h2>

            <div className="navbar__navigation">

                {!closeNavOnly() && (
                        <>
                            <RouterLink click={openProducts}>
                                <Icon name="dropbox"/>
                            </RouterLink>
                            
                            <RouterLink>
                                <Icon name="flip"/>
                            </RouterLink>

                            <span className="spacer"></span>

                            <RouterLink click={() => dispatch(sidebarSlice.actions.open())}>
                                <Icon name="hamburger"/>
                            </RouterLink>
                        </>
                )}
                

                {
                    closeNavOnly() &&
                    <RouterLink route={routes.home}>
                        <Icon name="close-gray"/>
                    </RouterLink>
                }
                

            </div>

        </div>   
    )
}

export default NavBar;