import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import Accordion from '../../../components/ui/Accordion';
import Checkbox from '../../../components/ui/Checkbox';
import { categoryItems, lensThickness } from '../../../constants/my-companion.constants';
import iMyCompanionStore from '../../../store/interface/mycompanion-store.interface';
import { getMyCompanion } from '../../../store/slices/mycompanion.slice';

interface Props {

}

const MyCompanionList : React.FC<Props>  = () =>  {
    // VARIABLES
    const myCompanionStore: iMyCompanionStore = useSelector(getMyCompanion);
    const [selectedThickness, setSelectedThickness] = useState<number[]>([])

    // HOOKS

    // METHODS
    const priceContent = (price: number) => {
        if(myCompanionStore.isPriceOff) return null;
        return <span className="my-companion-list__price">
                    {price} €
               </span>;
    }

    const showCategoryLens = () => {
        return myCompanionStore.category === 'correct' ||
        myCompanionStore.category === 'enhance' ||
        myCompanionStore.category === 'protect'
    }

    const showLensThickness = () => {
        return myCompanionStore.category === 'thickness'
    }

    const onLensThicknessSelect = (expanded: boolean, thickness: number) => {
        let newValue;
        if(expanded) {
            newValue = [...selectedThickness, thickness];
        } else {
            newValue = selectedThickness.filter( x => x !== thickness);
        }
        setSelectedThickness(newValue)
    }

    return (
        
        <div className="my-companion-list">
            {   
                showCategoryLens() && 
                categoryItems.map( (cat, index) => (
                    <Accordion title={cat.label} rightTitle={priceContent(cat.price)} key={index}>
                        
                        {
                            cat.subItems.map( (subCat, subIndex) => (
                                <Accordion title={subCat.label} type="child" rightTitle={priceContent(subCat.price)} key={subIndex}/>
                            ))
                        }
                        
                    </Accordion>
                ))
            }


            {
                showLensThickness() &&
                lensThickness.map( (thickness, index ) => (
                    <Accordion 
                        clicked={(expanded) => onLensThicknessSelect(expanded, thickness)}
                        title={String(thickness)} 
                        key={index} 
                        rightTitle={<Checkbox checked={selectedThickness.includes(thickness)}/>}/>
                ))
            }
        </div>
    )
}

export default MyCompanionList;