import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from '../../../components/Modal';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import iMyCompanionStore from '../../../store/interface/mycompanion-store.interface';
import myCompanionSlice, { getMyCompanion } from '../../../store/slices/mycompanion.slice';

interface Props {

}

const MyCompanionFooter : React.FC<Props>  = () =>  {
    // VARIABLES
    const myCompanionStore: iMyCompanionStore = useSelector(getMyCompanion);
    const [priceOff, setPriceOff] = useState(true);
    const [showSaveModal, setShowSaveModal] = useState(false);
    const dispath = useDispatch();

    // HOOKS
    useEffect( () => {
        if(myCompanionStore.isPriceOff !== undefined){
            setPriceOff(myCompanionStore.isPriceOff);
        }
    }, [myCompanionStore.isPriceOff])

    // METHODS
    const priceOffOnclickHandler = () => {
        dispath(myCompanionSlice.actions.offPrice(!priceOff));
        setPriceOff(!priceOff);
    }

    const save = () => {
        setShowSaveModal(true)
    }

    return (
        <div className="my-companion-footer">
            
            <Button 
                text={!priceOff ? 'Price ON' : 'Price OFF'} 
                type={!priceOff ? 'primary' : 'secondary'} 
                 
                click={priceOffOnclickHandler}/>

            {
                !priceOff &&
                <Button 
                    text="Essilor recommended prices" 
                    type="secondary" 
                     
                    icon="adjust"/>
            }
            
            

            <Button text="Save" icon="check-circle" click={save}/>

            <Modal 
                onClose={() => setShowSaveModal(false)}
                height="23rem" width="26rem"
                show={showSaveModal} hideCloseButton={true}>
                
                <div className="my-companion-footer__saved">
                    <Icon name="saved"></Icon>
                    <h2>Saved</h2>
                </div>

            </Modal>
        </div>
    )
}

export default MyCompanionFooter;