import React from 'react';
import useBEM from '../../utils/hooks/useBEM';
// import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    type?: 'default' | 'light' | 'white'
}

const Text : React.FC<Props>  = ({
    children,
    type = 'default'
}) =>  {
    // VARIABLES
    const [B] = useBEM('text');

    // HOOKS

    // METHODS
    const modifierClass = () => {
        const classes = [];
        classes.push(type);
        return classes;
    }

    return (
        <p className={B(modifierClass())}>{children}</p>
    )
}

export default Text;