import React from 'react';
import { useDispatch } from 'react-redux';
import productsSlice from '../../../store/slices/products.slice';
import useBEM from '../../../utils/hooks/useBEM';
import Arrow from '../../ui/Arrow';
import Icon from '../../ui/Icon';

interface Props {
    open: boolean,
    title: string,
    products: any[]
}

const SubProducts : React.FC<Props>  = ({
    open = false,
    title,
    products
}) =>  {
    // VARIABLES
    const dispatch = useDispatch();
    const [B, E] = useBEM('sub-products');

    // HOOKS

    // METHODS
    const demoProduct = (product: any) => {
        dispatch(productsSlice.actions.selectProductTodemo(product))
        dispatch(productsSlice.actions.changeView('demo'));
    }

    const onClose = () => {
        dispatch(productsSlice.actions.selectBrandTodemo(undefined));
    }

    return (
        <div className={B( !open ? 'closed' : '' )}>
            
            <div className={E('head')} onClick={onClose}>
                <h5>{title}</h5>
                <Arrow type="up" color="#fff"/>
            </div>

            <ul className={E('list')}>
                {
                    products.map((p, index) => (
                        <li key={index}>
                            {p.label}
                            <Icon name="eye-blue" click={() => demoProduct(p)}/>
                        </li>
                    ))
                }
            </ul>

        </div>
    )
}

export default SubProducts;