import React from 'react';
import useBEM from '../../../utils/hooks/useBEM';
import { useDispatch, useSelector } from 'react-redux';
import educationSlice, { getEducation } from '../../../store/slices/education.slice';
import ToggleRadio from '../../ui/ToggleRadio';
import EducationStep from './EducationSteps';
import Arrow from '../../ui/Arrow';

interface Props {

}

const EducationContent : React.FC<Props>  = () =>  {
    // VARIABLES
    const { isItemEnabled, isLensEnabled, environment } = useSelector(getEducation);
    const dispatch = useDispatch();

    const [B, E] = useBEM('education-content');
    const { isControlsOpen } = useSelector(getEducation)

    const toggleItem = (value: boolean) => {
        dispatch(educationSlice.actions.toggleItemControl());
    }

    const toggleLens = (value: boolean) => {
        dispatch(educationSlice.actions.toggleLensControl());
    }

    const educationSteps = [
        {
            instruction: 'First Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu. Etiam gravida est vel dolor.',
            guide: <>                
                <ToggleRadio on={isItemEnabled} onChange={toggleItem}/>
                <label className={isItemEnabled ? 'active' : ''}>Activate the item to see the impact</label>
            </>,
        },
        {
            instruction: 'Second Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu.',
            guide: <>
                <div className="circle">
                    <Arrow type="right" color="#fff" size=".3rem"/>
                </div>
                <label className="active">Move the item to understand what happens</label>
            </>
        },
        {
            instruction: 'Third Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu. Aliquam',
            guide: <>                
                <ToggleRadio on={isLensEnabled} onChange={toggleLens}/>
                <label className={isLensEnabled ? 'active' : ''}>Add Essilor’s lenses to see the difference</label>
            </>
        },
        {
            instruction: 'Fourth Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu.',
            guide: <>
                <div className="circle">
                    <Arrow type="right" color="#fff" size=".3rem"/>
                </div>
                <label className="active">Move the image on the retina</label>
            </>
        },
        {
            instruction: 'Fifth Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum.',
        },
    ]

    // HOOKS

    // METHODS
    const toggleControls = () => {
        dispatch(educationSlice.actions.toggleControls());
    }

    return (
        <div className={B(environment)}>

            <button 
                className={E('toggle-controls', [!isControlsOpen ? 'collapsed' : ''])} 
                onClick={toggleControls}>
                <span></span>
            </button>


            <EducationStep steps={educationSteps} environment={environment}/>

        </div>
    )
}

export default EducationContent;