import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import prescriptionSlice, { getPrescription } from '../../../store/slices/prescription.slice';
import PrecisionType from '../../../types/precision.type';
import useBEM from '../../../utils/hooks/useBEM';
import PrescriptionInput from './PrescriptionInput';

interface Props {
    withPrecision?: boolean
}

const PrescriptionForm : React.FC<Props>  = ({
    withPrecision = true
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('prescription-form');
    const { precision } = useSelector(getPrescription);
    const dispatch = useDispatch();


    // HOOKS

    // METHODS
    const precisionActiveClass = (value: string) => {
        return precision === value ? 'active' : ''
    }

    const changePrecision = (value: PrecisionType) => {
        dispatch(prescriptionSlice.actions.changePrecision(value))
    }

    return (
        <div className={B()}>

            {
                withPrecision && 
                <div className={E('precision')}>
                    <button className={ precisionActiveClass('standard') } onClick={() => changePrecision(PrecisionType.STANDARD)}>
                        Standard Precision
                    </button>
                    <button className={ precisionActiveClass('ava') } onClick={() => changePrecision(PrecisionType.AVA)}>
                        AVA™ Precision
                    </button>
                </div>
            }


            <div className={E('field')}>
                <label>Sphere (SPH)</label>
                <PrescriptionInput label="OD" value="+4,27"/>
                <PrescriptionInput label="OS" value="+1,52"/>
            </div>

            <div className={E('field')}>
                <label>Cylinder (CYL)</label>
                <PrescriptionInput label="OD" value="+3,22"/>
                <PrescriptionInput label="OS" value="+2,51"/>                  
            </div>

            <div className={E('field')}>
                <label>Axis (AXE)</label>
                <PrescriptionInput label="OD" value="+0,24"/>
                <PrescriptionInput label="OS" value="+2,28"/>                
            </div>

            <div className={E('field')}>
                <label>Addition (ADD)</label>
                <PrescriptionInput value="+0,28"/>
            </div> 
        </div>
    )
}

export default PrescriptionForm;