import React, { ChangeEvent, useEffect, useState } from 'react';

interface Props {
    label?: string,
    placeholder?: string
}

const MaterialTextInput : React.FC<Props>  = ({
    label,
    placeholder
}) =>  {
    // VARIABLES
    // const ref = useRef<HTMLInputElement>(null);
    const [value , setValue] = useState<string>();

    // HOOKS
    useEffect(() => {
        // ref.current?.addEventListener('focus' , () => {
        //     // alert('a');
        // }, false)
    }, [])

    // METHODS
    const inputOnChange = (event: ChangeEvent<HTMLInputElement>) => {
        setValue(event.target.value);
    }

    return (
        <div className={`material-textinput ${value ? 'material-textinput--with-value' : ''}`}>
            <input 
                // ref={ref} 
                value={value}
                onChange={inputOnChange}
                className="material-textinput__input" type="text" placeholder={placeholder}/>

            {
                label && <label className="material-textinput__label">{label}</label>
            }
        </div>
    )
}

export default MaterialTextInput;