import React, { Fragment } from 'react';
import TriLens from '../../../components/TriLen';
import Icon from '../../../components/ui/Icon';
import Text from '../../../components/ui/Text';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    // TEMPORARY
    pair: number
}

const DeliveryPairConsultation : React.FC<Props>  = ({
    pair
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('delivery-pair-consultation');
    const protectTempData = {
        title: 'EyeProtect System',
        details: [
            { text:'Protection against harmful UV Light', icon: 'protect-uv-light' },
            { text:'Helps protect from harmful blue light', icon: 'protect-blue-light' },
            { text:'Azsthetically clear lenses', icon: 'clear-lens' }
        ]
    }

    const correctTempData = {
        title: 'EyeProtect System',
        details: [
            { text:'Sharp Vision', icon: 'sharp-vision-blue' },
            { text:'Smooth transition near to far', icon: 'smooth-transition' },
            { text:'Eliminates off-balance', icon: 'eliminates-off-balance' }
        ]
    }

    // HOOKS

    // METHODS
    const renderLensData = ({
        title,
        details
    }: any) => {
        return (
            <>
                <label>{title}</label>
                <ul>
                    {
                        details.map( (item: any, index: number) => (
                            <Fragment key={index}>
                                <li>
                                    <Icon name={item.icon} type="image"/>
                                    <p>{item.text}</p>
                                </li>
                                {
                                    details.length !== (index + 1) && 
                                    <li className="separator"></li>
                                }                                
                            </Fragment>
                        ))
                    }
                </ul>
            </>
        )
    }

    return (
        <div className={B()}>
            <div className={E('lens')}>
                <TriLens/>
            </div>

            <div className={E('details')}>
                <h2>Lens Consultation</h2>

                <Text type="light">
                    Integer a quam ut odio tincidunt scelerisque. Integer dictum imperdiet metus non placerat. Aenean ut lectus ut felis vehicula efficitur in id quam. Aenean tristique ultrices rhoncus. Donec ullamcorper, magna eu elementum porta, enim urna varius ante, ac consectetur velit ex sed augue.
                </Text>

                {/* TEMPORARY */}
                { pair === 1 && <>
                        <label>Correct</label>
                        <div className={E('lens-type')}>
                            {renderLensData(correctTempData)}
                        </div>                    
                </>}


                <label>Protect</label>
                <div className={E('lens-type')}>
                    {renderLensData(protectTempData)}
                    {renderLensData(protectTempData)}                    
                </div>

                {/* TEMPORARY */}
                { pair === 1 && <>
                        <label>Enhance</label>
                        <div className={E('lens-type')}>
                            {renderLensData(correctTempData)}
                        </div>                    
                </>}


            </div>
        </div>
    )
}

export default DeliveryPairConsultation;