import React, { Fragment, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import Modal from '../../../components/Modal';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import routes from '../../../constants/routes.constant';
import iLensConsultationType from '../../../interface/lens-consultation-type.interface';
import lensConsultationSlice, { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import useBEM from '../../../utils/hooks/useBEM';
import useUtils from '../../../utils/hooks/useUtils';

interface Props {

}

const LensConsultationType : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-consultation-type');
    const [ cloneObject ] = useUtils();
    const { data, lensProducts } = useSelector(getLensConsultation);
    const [ validatePackageOfferModal, setValidatePackageOfferModal ] = useState(false);
    const dispatch = useDispatch();
    const history = useHistory();

    // HOOKS

    // METHODS
    const selectType = (type: iLensConsultationType) => {
        dispatch(lensConsultationSlice.actions.selectLensType(type));
        dispatch(lensConsultationSlice.actions.changeView('brand'));
    }

    const getSavedValue = (type: 'correct' | 'protect' | 'enhance') => {
        if(lensProducts){
            return lensProducts[type].subProduct?.label;            
        }
    }

    const deleteValue = (type: string) => {
        const newState = {
            ...lensProducts,
            [type]: {
                product: null,
                subProduct: null
            }
        }
        dispatch(lensConsultationSlice.actions.selectLensProduct(newState))
    }

    const renderListItem = (type: iLensConsultationType) => {
        const currentValue = getSavedValue(type.type as 'correct');
        
        return (
            <li className={E('list-item', [currentValue ? 'with-value' : ''])} >

                <h2>
                    {type.label}
                </h2>

                { 
                    !currentValue &&  
                    <Button  
                        click={() => selectType(type)} 
                        icon="plus"                          
                        type="secondary" 
                        circle={true}/> 
                }

                {
                    currentValue && 
                    <div className={E('list-item-value')}>
                        {currentValue}
                        <Icon name="trash" click={() => deleteValue(type.type)}/>
                    </div>
                }
            </li>
        )
    }

    const withLensProductsSelected = () => {
        let withProducts = false;
        const cloned = cloneObject<any>(lensProducts);
        ['correct','protect','enhance'].forEach( (key: any) => {
            if(cloned[key].subProduct) withProducts = true;
        });
        return withProducts;
    }

    const gotoPackageOffer = () => {
        const proceed = withLensProductsSelected();
        if(!proceed) {
            setValidatePackageOfferModal(true);
            return;
        }
        history.push(routes.packageOffer.path);
    }

    return (
        <div className={B()}>

            <ul className={E('list')}>
                <li className={E('list-item')} onClick={gotoPackageOffer}>
                    Package Offer 
                    <Icon name="stacked-group2"/>
                </li>
                {
                    data.map( type => (
                        <Fragment key={type.type} >
                            {renderListItem(type)}
                        </Fragment>
                    ))
                }
            </ul>


            <Modal show={validatePackageOfferModal} height="32.5rem" onClose={() => setValidatePackageOfferModal(false)}>
                <div className={E('modal')}>
                    <div className={E('modal-icon')}>
                        <Icon name="exclamation"/>
                    </div>
                    <p className={E('modal-text')}>To have access to Packaged Offer<br/> you must fill in at least one information.</p>
                    <Button 
                        click={() => setValidatePackageOfferModal(false)}
                        text="Fill the Lens Consultation" 
                        type="secondary" weight="normal"/>
                </div>
            </Modal>
        </div>
    )
}

export default LensConsultationType;