import React, { useState } from 'react';
import iQuestionChoice from '../../../../interface/question-option.interface';
import useBEM from '../../../../utils/hooks/useBEM';
import ReactHtmlParser from 'react-html-parser';
import Icon from '../../../../components/ui/Icon';

interface Props {
    options?: iQuestionChoice[]
}

const ChoiceBoxImage : React.FC<Props>  = ({
    options = []
}) =>  {
    // VARIABLES
    const  [B, E] = useBEM('choice-box-image');
    const [selected, setSelected] = useState<iQuestionChoice[]>([]);

    // HOOKS

    // METHODS
    const select = (choice: iQuestionChoice) => {
        // setSelected(choice);
        const isExist = selected?.filter(c => c.value === choice.value);

        if( isExist.length > 0) {
            setSelected(selected?.filter(c => c.value !== choice.value))
        } else {
            setSelected([
                ...selected,
                choice
            ])
        }
    }

    const selectedChoiceClassName = (choice: iQuestionChoice) => {
        const isSelected = selected?.filter(c => c.value === choice.value)
        return [isSelected.length > 0 ? 'selected' : '']
    }

    return (
        <ul className={B()}>
            {
                options.map(option => (
                    <li 
                        key={option.value}
                        className={E('item', selectedChoiceClassName(option))}                     
                        onClick={() => select(option)}>
                        
                        <Icon type="image" name={option.image || ''} className={E('image')} background={true}/>

                        <span className={E('check')}></span>

                        <label>{ReactHtmlParser(option.label)}</label>
                    </li>
                ))
            }            
        </ul>
    )
}

export default ChoiceBoxImage;