import React, { useState } from 'react';
import Arrow from './Arrow';

interface Props {
    title: string,
    rightTitle?: React.ReactNode,
    type?: 'parent' | 'child',
    clicked?: (expanded: boolean) => void
}

const Accordion : React.FC<Props>  = ({
    title,
    children,
    rightTitle,
    type = 'parent',
    clicked
}) =>  {
    // VARIABLES
    const [open, setOpen] = useState(false);

    // HOOKS

    // METHODS
    const onClickHandler = () => {
        setOpen(!open);
        if(clicked)clicked(!open);
    }

    return (
        <div className={`accordion accordion--${type}`}>
            <div 
                onClick={onClickHandler}
                className={`accordion__head ${open ? 'accordion__head--open' : ''}`}>
                
                <label>{title}</label>


                { rightTitle && 
                    <div className="accordion__right-title">
                        {rightTitle}
                    </div>
                }

                { children && <Arrow type={open ? 'up' : 'down'} size=".75rem"/> }
            </div>
            {
                open && children &&
                <div className="accordion__body">
                    {children}
                </div>
            }
            
        </div>
    )
}

export default Accordion;