import React from 'react';
import Icon from '../../ui/Icon';
import iMenu from '../../../interface/menu.interface';
import { useDispatch, useSelector } from 'react-redux';
import sidebarSlice, { getSidebar } from '../../../store/slices/sidebar.slice';

interface Props {
    disabled?: boolean,
    title?: string,
    items: iMenu[]
}

const Menu : React.FC<Props>  = ({
    title,
    items,
    disabled = false
}) =>  {
    // VARIABLES
    const {selectedMenu} = useSelector(getSidebar);
    const dispath = useDispatch()
    // HOOKS

    // METHODS
    const selectMenu = (menu: iMenu) => {
        if(disabled){
            if(selectedMenu && selectedMenu.id === menu.id){
                dispath(sidebarSlice.actions.selectMenu(undefined));
                return;
            }
            return;
        }
        if(!menu?.submenu) {
            dispath(sidebarSlice.actions.close())
        }
        dispath(sidebarSlice.actions.selectMenu(menu))
    }
    
    const isActiveMenu = (menu: iMenu) => {
        if(!selectedMenu)return false;        
        return (selectedMenu.id === menu.id);
    }
    const activeMenuClass = (menu: iMenu) => {
        return isActiveMenu(menu) ? 'menu__item--active' : '';
    }

    const renderMenu = (items:iMenu[] = []) => {
        return items.map( (menu, index) => {
            return (                
                <li 
                    onClick={() => selectMenu(menu)}
                    className={`menu__item ${activeMenuClass(menu)}`} 
                    key={index}>
                    {
                        <Icon name={isActiveMenu(menu) && menu.activeIcon ? menu.activeIcon : menu.icon}/>
                    }
                    <h5>{menu.label}</h5>
                </li>
            )
        })
    }

    return (
        <div className={`menu ${disabled ? 'menu--disabled' : ''}`}>
                {   
                    title && 
                    <label className="menu__title">
                        {title}
                    </label>
                }

                <ul className="menu__items">
                    {renderMenu(items)}       
                </ul>
        </div>
        
    )
}

export default Menu;