import React, { useState } from 'react';
import useBEM from '../../../../utils/hooks/useBEM';
import Slider from 'react-input-slider';
import iQuestionSlider from '../../../../interface/question-slider.interface';
import useUtils from '../../../../utils/hooks/useUtils';

interface Props {
    sliders?: iQuestionSlider[]
}

const SliderGroup : React.FC<Props>  = ({
    sliders = []
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('slider-group');
    const [ cloneObject ] = useUtils();
    const [state, setState] = useState<iQuestionSlider[]>(sliders);

    const sliderStyle = {
        track: {
            backgroundColor: "#CFD3D7"
        },
        active: {
            backgroundColor: "#0386D6",
            borderRadius: "1em"
        },
        thumb: {
            width: 20,
            height: 20,
            opacity: 1
        }
    }
    // HOOKS

    // METHODS
    const handleOnChange = (data: any, index: number) => {
        const newStates: iQuestionSlider[] = cloneObject<any>(state);
        newStates[index].value = data.x;
        setState(newStates);
    }

    return (
        <div className={B()}>
            {
                state.map( (slider, index) => (
                    <div className={E('slider-box')} key={index}>
                        <label className={E('slider-label')}>
                            {slider.label}
                        </label>

                        <div className={E('slider')}>
                            <Slider 
                                xstep={slider.step || 1} 
                                xmin={slider.min || 0}
                                xmax={slider.max || 100}
                                axis="x" 
                                x={slider.value}
                                onChange={(data) => handleOnChange(data, index)}
                                styles={sliderStyle}/>
                        </div>
                    </div>
                ))
            }

        </div>
    )
}

export default SliderGroup;