import React, { useState } from 'react';
import HalfCircleProgress from '../../../components/ui/HalfCircleProgress';
import Icon from '../../../components/ui/Icon';
import useBEM from '../../../utils/hooks/useBEM';
import Slider from "react-input-slider";
import Button from '../../../components/ui/Button';
import { useDispatch } from 'react-redux';
import lensThicknessSlice from '../../../store/slices/lens-thickness.slice';


interface Props {

}

const LensThicknessControl : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-thickness-control');
    const [slider, setSlider] = useState(0);
    const dispatch = useDispatch();

    // HOOKS

    // METHODS
    const viewPrescription = () => {
        dispatch(lensThicknessSlice.actions.changeView('prescription'));
    }

    return (
        <div className={B()}>
            
            <div className={E('prescription')}>
                Prescription                
                <span>Completed</span>
                <Icon name="pencil" click={viewPrescription}/>
            </div>

            <div className={E('controls')}>
                
                <div className={E('controls-head')}>
                    <label>Lens Thickness</label> 
                    <span>1.6</span>
                </div>

                <div className={E('controls-half-circles')}>                    
                    <HalfCircleProgress value={-50} label="Weight" />
                    <HalfCircleProgress value={-5} label="Center Thickness*" />
                    <HalfCircleProgress value={2} label="Edges Thickness*" />
                </div>

                <div className={E('controls-slider')}>
                    <Icon name="plus-circle-small"/>
                    <Slider
                            styles={{
                                active: {
                                backgroundColor: "#0386D6",
                                },
                            }}
                            axis="x"
                            xstep={10}
                            xmin={0}
                            xmax={70}
                            x={slider}
                            onChange={({ x }) => setSlider(x)}
                    />
                    <Icon name="minus-circle-small"/>
                    <label className={E('controls-slider-index')}>
                        {new Array(8).fill('').map((item, index) => (
                            <span key={index}></span>
                        ))}
                    </label>
                </div>


                <label className={E('controls-instruction')}>*Compared to the reference index</label>
                <Button text="View more details" icon="right-arrow-circle-white"/>
            </div>

        </div>
    )
}

export default LensThicknessControl;