import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import SideBoxBordered from '../../../components/ui/SideBoxBordered';
import lensImages from '../../../constants/lens-consultation-lens-images.constants';
import { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import useBEM from '../../../utils/hooks/useBEM';
import Slider from "react-input-slider";
import { useHistory } from 'react-router';
import routes from '../../../constants/routes.constant';
import useUtils from '../../../utils/hooks/useUtils';


interface Props {

}

const LensConsultationSide : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-consultation-side');
    const [ cloneObject ] = useUtils();
    const history = useHistory();
    const { lensProducts, lensType } = useSelector(getLensConsultation);
    const lensImageSet: any = lensImages;
    const [sliderValue, setSliderValue] = useState(50);
    const sliderStyle = {
        active: {
            backgroundColor: "CCE7F7",
        },
        thumb: {
            width: 24,
            height: 24,
            opacity: 1,
        }
    }

    // HOOKS

    // METHODS
    const withLensProductsSelected = () => {
        let withProducts = false;
        const cloned = cloneObject<any>(lensProducts);
        ['correct','protect','enhance'].forEach( (key: any) => {
            if(cloned[key].subProduct) withProducts = true;
        });
        return withProducts;
    }

    const setLensOpacity = (value: number) => setSliderValue(value);

    const getActiveLensImageSet = () => {
        if(lensProducts?.protect.subProduct) {
            return lensImageSet.transition;
        }
        return lensImageSet[lensType ? lensType.type : 'notype'];
    }

    const generateTransitionLensOpacity = (flag: string) => {
        if (flag !== 'transition') return {}
        return {
            opacity: sliderValue / 100
        }
    }

    const activateTransitionSlide = () => {
        return lensProducts?.protect.subProduct ? true : false;
    }

    const gotoLensThickness = () => {
        history.push(routes.lensThickness.path);
    }

    const gotoDemo = () => {
        // history.push(routes.demonstration.path);
    }

    return (
        <div className={B()}>
            <SideBoxBordered>
                
                <div className={E('top')}>                    
                    <label>
                        <Icon name="progress-circle"/> 
                        { lensType ? lensType?.label : 'Prescription'}
                    </label>

                    <div className={E('lens-pairs')}>
                        <Icon name="lens-pair-1"/>
                        <span></span>
                        <Icon name="lens-pair-2"/>
                    </div>
                </div>


                {
                    activateTransitionSlide() && 
                    <div className={E('slider')}>
                        <Icon name="sun-high"/>
                        <Slider
                            styles={sliderStyle}
                            axis="y"
                            ystep={1}
                            ymin={90}
                            ymax={10}
                            y={sliderValue}
                            onChange={({ y }) => {setLensOpacity(y)}}
                        />
                        <Icon name="sun-low"/>
                    </div>
                }


                <div className={E('lens', [lensType && !activateTransitionSlide()? 'with-lens-type' : ''])}>                                        
                    {
                        getActiveLensImageSet().map( (img: any, index: number) => (
                            <Icon 
                                customStyle={generateTransitionLensOpacity(img.class)}
                                className={img.class}
                                name={img.name} 
                                type={img.type} 
                                key={index}/>
                        ))
                    }
                </div>

                {
                    withLensProductsSelected() &&
                    <div className={E('buttons')}>
                        <Button text="Demo" type="secondary" click={gotoDemo}/>
                        <Button text="Save & Next" icon="right-arrow-circle-white" click={gotoLensThickness}/>
                    </div>
                }

            </SideBoxBordered>
        </div>
    )
}

export default LensConsultationSide;
