import React, { Fragment } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import Checkbox from '../../../components/ui/Checkbox';
import iLensProposalField from '../../../interface/lens-proposal-field.interface';
import prescriptionSlice, { getPrescription } from '../../../store/slices/prescription.slice';
import LensProposalSectionType from '../../../types/lens-proposal-section.type';
import PrescriptionViewType from '../../../types/prescription-view.type';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

}

const PrescriptionLensProposal : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('prescription-lens-proposal');
    const { generalEquipment, complementaryEquipment } = useSelector(getPrescription)
    const dispatch = useDispatch();

    // HOOKS

    // METHODS
    const back = () => {
        dispatch(prescriptionSlice.actions.changeView(PrescriptionViewType.PRESCRIPTION));
    }

    const onSelectFieldOption = (section: LensProposalSectionType, field:iLensProposalField, option: any) => {

        const dataToUpdate = (section === LensProposalSectionType.GENERAL) ? generalEquipment : complementaryEquipment;

        const newState: iLensProposalField[] = dataToUpdate.map( f => {
            if (f.field === field.field) {

                //IF SAME VALUE, UNSELECT FIELD
                const isSameValue = field.value?.value === option.value;

                return { ...f, value: !isSameValue ? option : null}
            }
            return f;
        });

        dispatch(prescriptionSlice.actions.updateLensEquipment({
            data: newState,
            storeField: section
        }));
    }

    const renderFiendOptions = (field: iLensProposalField, section: LensProposalSectionType) => {
        return field.options.map( (option: any) => (
            <li 
                className={`list__item list__item--${field?.value?.value === option.value ? 'selected' : ''}`} 
                onClick={() => onSelectFieldOption(section ,field, option)} 
                key={option.value}>
                
                {option.label}
                <Checkbox checked={field?.value?.value === option.value}/>

            </li>
        ))
    }

    const renderFields = (fields: iLensProposalField[], section: LensProposalSectionType) => {
        return fields.map((field: any) => (
            <Fragment key={field.field}>
                { field.label && <label className={E('list-label')}>{field.label}</label> }
                <ul className="list">
                    {renderFiendOptions(field, section)}
                </ul>
            </Fragment>
        ))
    }

    return (
        <div className={B()}>
            
            <Button 
                click={back}
                text="Back" 
                icon="left-arrow-circle-gray" 
                iconPosition="left"
                type="secondary" 
                />


            <div className={E('scroll')}>

                <div className={E('section')}>
                    <label>GENERAL EQUIPMENT</label>                
                </div>
                {renderFields(generalEquipment, LensProposalSectionType.GENERAL)}

                <div className={E('section')}>
                    <label>COMPLEMENTARY EQUIPMENT</label>
                </div>
                {renderFields(complementaryEquipment, LensProposalSectionType.COMPLEMENTARY)}

            </div> 
            
        </div>   
    )
}

export default PrescriptionLensProposal;