import React, { ChangeEventHandler, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import productDemoSlice from '../../../store/slices/products-demo.slice';
import productsSlice, { getProducts } from '../../../store/slices/products.slice';
import useBEM from '../../../utils/hooks/useBEM';
import ProductDemo from './ProductDemo';
import ProductDemoSelection from './ProductDemoSelection';

interface Props {

}

const ProductDemoCompare : React.FC<Props>  = () =>  {
    // VARIABLES
    const dispatch = useDispatch();
    const [B, E] = useBEM('product-demo-compare');
    const { brandToDemo, productToDemo, productToCompare } =  useSelector(getProducts);
    const [sliderValue, setSliderValue] = useState(50);


    // HOOKS
    useEffect(() => {
        dispatch(productsSlice.actions.selectProductToCompare(productToDemo));
    }, [])

    // METHODS
    const back = () => {
        dispatch(productDemoSlice.actions.disableControls(false));
        dispatch(productDemoSlice.actions.enableCompare(false));
    }
    const onSelectProduct = (product: any) => {
        dispatch(productsSlice.actions.selectProductTodemo(product));
    }

    const onSelectProductToCompare = (product: any) => {
        dispatch(productsSlice.actions.selectProductToCompare(product));
    }

    const sliderOnChange = (slider: any) => {
        setSliderValue(slider.target.value);
    }

    return (
        <div className={B()}>
            <ProductDemo />  

            <Button 
                className={E('back')}
                outlineType="light"
                text="Back" 
                icon="left-arrow-circle-gray" 
                iconPosition="left" type="secondary" 
                click={back}
            />

            <div className={E('splitter')} style={{
                left:`${sliderValue}%`
            }}>
                <span></span>
            </div>

            <input 
                value={sliderValue} onChange={sliderOnChange}
                type="range" min="0" max="100"  className={E('slider')}/>

            <div className={E('product-selections')}>
                <ProductDemoSelection
                    onSelectProduct={(p) => onSelectProduct(p)}
                    selectedProduct={productToDemo}
                    products={brandToDemo.products}
                />

                {
                    productToCompare && 
                    <ProductDemoSelection
                        onSelectProduct={(p) => onSelectProductToCompare(p)}
                        selectedProduct={productToCompare}
                        products={brandToDemo.products}
                    />
                }

            </div>
        </div>
    )
}

export default ProductDemoCompare;