import React from 'react';
import useBEM from '../../../utils/hooks/useBEM';
import DeliveryPairConsultation from './DeliveryPairConsultation';
import DeliveryPairDetails from './DeliveryPairDetails';
import DeliveryPairMeasures from './DeliveryPairMeasure';

interface Props {
    details: {
        pair:number,
        type: string,
        price: string,
        description: string    
    }
}

const DeliveryPair : React.FC<Props>  = ({
    details
}) =>  {
    // VARIABLES
    const [B] = useBEM('delivery-pair');

    // HOOKS

    // METHODS

    return (
        <div className={B()}>
                        
            <DeliveryPairDetails 
                pair={details.pair}
                type={details.type}
                description={details.description}
                price={details.price}
            />     
            <DeliveryPairConsultation pair={details.pair}/>    
            <DeliveryPairMeasures/>       

        </div>
    )
}

export default DeliveryPair;