import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import iLensConsultationBrand from '../../../interface/lens-consultation-brand.interface';
import lensConsultationSlice, { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

}

const LensConsultationBrand : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-consultation-brand');
    const { lensType } = useSelector(getLensConsultation);
    const dispatch = useDispatch();

    // HOOKS

    // METHODS
    const back = () => {

        dispatch(lensConsultationSlice.actions.selectLensType(null));
        dispatch(lensConsultationSlice.actions.changeView('type'));
    }

    const selectBrand = (brand: iLensConsultationBrand) => {
        dispatch(lensConsultationSlice.actions.selectLensBrand(brand));
        dispatch(lensConsultationSlice.actions.changeView('product'));
    }

    return (
        <div className={B()}>
            
            <Button 
                click={back}
                text={lensType?.label} 
                icon="left-arrow-circle-gray" type="secondary" 
                 
                iconPosition="left"/>


            <ul className={E('list')}>                
                {
                    lensType?.brands.map( (brand) => (
                        <li key={brand.value} onClick={() => selectBrand(brand)}>
                            {brand.label}
                            <Icon name="right-arrow-circle-blue"/>
                        </li>
                    ))
                }
            </ul>
        </div>
    )
}

export default LensConsultationBrand;