import React from 'react';
import { useSelector } from 'react-redux';
import { getNeedExplorer } from '../../../store/slices/need-explorer.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

}

const NeedExplorerCategory : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('need-explorer-category');
    const { questions, categoryStep } = useSelector(getNeedExplorer);

    // HOOKS

    // METHODS

    return (
        <div className={B()}>
            <ul className={E('items')}>
                
                {
                    questions.map( (category, index) => (
                        <li className={E('item', [index === categoryStep ? 'active' : ''])} key={category.id}>
                            <label>
                                {category.category}
                            </label>
                        </li>
                    ))
                }

            </ul>
        </div>
    )
}

export default NeedExplorerCategory;