import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { complementaryEquipment, generalEquipments } from '../../constants/prescription-lens-proposal.constants';
import iLensProposalField from '../../interface/lens-proposal-field.interface';
import LensProposalSectionType from '../../types/lens-proposal-section.type';
import PrecisionType from '../../types/precision.type';
import PrescriptionViewType from '../../types/prescription-view.type';
import iPrescriptionStore from '../interface/prescription-store.interface';
import iRootState from '../interface/root-state.interface';


const initialState: iPrescriptionStore  = {
    precision: PrecisionType.STANDARD,
    view: PrescriptionViewType.PRESCRIPTION,
    generalEquipment: generalEquipments,
    complementaryEquipment: complementaryEquipment
}

const prescriptionSlice = createSlice({
    name: 'prescription',
    initialState,
    reducers: {
        changePrecision: (state, action: PayloadAction<PrecisionType>) => ({...state, precision: action.payload}),
        changeView: (state, action: PayloadAction<PrescriptionViewType>) => ({...state, view: action.payload}) ,
        updateLensEquipment: (state, action: PayloadAction<{data:iLensProposalField[], storeField: LensProposalSectionType}>) => {
            const storeProperty = `${action.payload.storeField}Equipment`;
            return {...state, [storeProperty]: action.payload.data}
        },

        // close: (state) => ({...state, open: false, selectedMenu: undefined }),
        // selectMenu: (state, action: PayloadAction<iMenu | undefined>) => ( {...state, selectedMenu: action.payload} ),
    }
});

export const getPrescription = (state:iRootState) => state.prescription;
export default prescriptionSlice;