import { Fragment , useEffect} from "react";

const HalfCircleProgress = ({label, value}: any) => {
    // const changeValue = () =>{

    // }
    useEffect(() => {
         const bar:any = document.getElementById(label);
         const negate = value < 0 && value > -50 ? -1 : 1
         let clip =  "0";
         let rotate = 0;
         if(value > -50 && value < 50){
            rotate = (((97 - (50 - value)) / 100) * 180 * negate) / 2;
            clip = value < 0 ? "rect(0, 4.6em, 3em, "+(Math.abs(value)/2)+"em)" : "rect(0, "+value+"em, 3em, 1em)";
         }
         else{
             rotate = (((97 - (97 - value)) / 100) * 180 * negate) / 2;
             clip =  "rect(0, 4.6em, 3em, 0em)";
             
         }
         bar.setAttribute(
           "style",
           "transform: rotate("+rotate+"deg);" +
           "clip: "+clip+";"
         );
    }, [value])
    return (
      <Fragment>
        <div className="half-progress">
          <div className="left"></div>
          <div className="right"></div>
          <div className="bar-overflow">
            <div className="bar"></div>
            <div className="bar-value" id={label}></div>
          </div>
          <span>{value}%</span>

          <span className="label-progress">{label}</span>
        </div>
      </Fragment>
    );
}
 
export default HalfCircleProgress;