import React, { useState } from 'react';
import CircularSlider from '@fseehawer/react-circular-slider';

interface Props {
    label?: string,
    value?: number
}

const Dial : React.FC<Props>  = ({
    label,
    value = 0
}) =>  {
    // VARIABLES
    const [state, setState] = useState(value);

    
    // HOOKS
    // useEffect(() => {
    //     alert(value +' + '+ initialValue)
    //     // setValue(0);
    // }, [value])

    // METHODS

    return (
        <div className="dial">
            <CircularSlider
                label={label}
                min={0}
                max={10}
                dataIndex={state}
                labelColor="#0386D6"
                labelBottom={true}
                knobColor="white"
                knobSize={50}
                progressColorFrom="#0386D6"
                progressSize={22}
                trackColor="rgb(204, 231, 247)"
                trackSize={22}
                valueFontSize="4.5rem"
                onChange={ (data:any) => { setState(data); } }

            ></CircularSlider>
        </div>
    )
}

export default Dial;