import React from 'react';
import Icon from '../../../components/ui/Icon';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    data: any[],
    title?: string,
    pair: number
}

const SummaryDetails : React.FC<Props>  = ({
    data,
    title,
    pair
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('summary-details');

    // HOOKS

    // METHODS

    return (
        <div className={B()}>
            <div className={E('label')}>
                <label>{title}</label>
                <span>Pair #{pair}</span>
            </div>

            <div className={E('wrapper')}>
                <div className={E('data')}>
                    {
                        data.map( (data: any,i: number) => (
                            <div className={E('lens')} key={i}>
                                <div className={E('lens-type')}>
                                    <Icon name={data.icon}/>
                                    <label>
                                        {data.title} <span>{data.sub}</span>
                                    </label>                                
                                </div>

                                <div className={E('lens-products')}>
                                    {
                                        data.products.map( (product: any, ii: number) => (
                                            <div className={E('lens-product')} key={ii}>
                                                <label>{product.title}</label>
                                                <ul>
                                                    {
                                                        product.descriptions.map((desc: any, iii: number) => (
                                                            <li key={iii}>
                                                                <Icon name={desc.icon}/>
                                                                <label>{desc.label}</label>
                                                            </li>              
                                                        ))
                                                    }
        
                                                </ul>
                                            </div>    
                                        ))
                                    }
                                </div>
                            </div>
                        ))
                    } 
                </div>
                <Icon name="lens" className={E('lens-image')}/>
            </div>

            <div className={E('lens-thickness')}>
                <Icon name="lens-thickness"/>
                <label>Lens Thickness</label>
                <span>1,5</span>
            </div>
        </div>
    )
}

export default SummaryDetails;