import React from 'react';
import { useSelector } from 'react-redux';
import { getNeedExplorer } from '../../../store/slices/need-explorer.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

}

const NeedExplorerProgress : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('need-explorer-progress');
    const { questions, categoryStep, questionStep } = useSelector(getNeedExplorer);

    // HOOKS

    // METHODS
    const totalNumberOfQuestions = () => {
        let numberOfQuestion = 0;
        questions.forEach( x => numberOfQuestion += x.questions.length);
        return numberOfQuestion;
    }

    const totalNumberOfAnsweredQuestions = () => {
        let numberOfAnsweredQuestions = 0;
        let category = categoryStep;
        while (category >= 0) {
            if(category !== categoryStep) {
                numberOfAnsweredQuestions += questions[category].questions.length;
            } else {
                numberOfAnsweredQuestions += questionStep;
            }

            category--;
        }
        return numberOfAnsweredQuestions;
    }

    const computeProgress = () => {
        const percent = 100 / totalNumberOfQuestions() * totalNumberOfAnsweredQuestions();
        return Math.ceil(percent);
    }

    return (
        <div className={B()}>
            <div className={E('text')}>
                <label>Patient</label>
                <label>{computeProgress()}%</label>
            </div>
            <div className={E('bar')}>
                <span style={{width: `${computeProgress()}%`}}></span>
            </div>
        </div>
    )
}

export default NeedExplorerProgress;