import React, { useEffect, useState } from 'react';
import Icon from './ui/Icon';
import Arrow from './ui/Arrow';
import RouterLink from './ui/RouterLink';
import routes from '../constants/routes.constant';
import { useLocation } from 'react-router';
import breadcrumbs from '../constants/breadcrumbs.contant';
import useUtils from '../utils/hooks/useUtils';
interface Props {

}

const BreadCrumb : React.FC<Props>  = () =>  {
    // VARIABLES
    const location = useLocation();
    const [ cloneObject ] = useUtils();
    const [ activeBreadCrumbs, setActiveBreadCrumbs ] = useState([]);

    // HOOKS
    useEffect( () => {
        const breadcrumbKey = getActiveRouteKey(location.pathname);
        const clonedbreadcrumbs = cloneObject<any>(breadcrumbs);
        setActiveBreadCrumbs(clonedbreadcrumbs[breadcrumbKey]);
    }, [location]);

    // METHODS
    const getActiveRouteKey = (path: string) => {
        const routeKeys = Object.keys(routes);
        const clonedRoutes = cloneObject<any>(routes);
        let activeRouteKey = routeKeys.filter( (k: string) => clonedRoutes[k].path === path)[0];
        return activeRouteKey;
    }

    return (
        <div className="breadcrumb">
            <ul>
                <li className="breadcrumb__item">
                    <RouterLink route={routes.home}>
                        <Icon name={activeBreadCrumbs.length > 0 ? 'home' : 'home-blue'} type="image" className="breadcrumb__home"/>                        
                    </RouterLink>
                </li>
                {
                    activeBreadCrumbs.map( (b: any, i) => (
                        <li className="breadcrumb__item" key={i}>
                            
                            <Arrow type="right" color="#b3b3b3"/>

                            <RouterLink route={b}>
                                {b.label}
                            </RouterLink>
                        </li>
                    ))
                }
            </ul>

        </div>
    )
}

export default BreadCrumb;