import React, { useState } from 'react';
import { useHistory } from 'react-router';
import Modal from '../../components/Modal';
import Button from '../../components/ui/Button';
import Icon from '../../components/ui/Icon';
import routes from '../../constants/routes.constant';
import summaryData from '../../constants/summary-data.constants';
import useBEM from '../../utils/hooks/useBEM';
import SummaryDetails from './components/SummaryDetails';
import SummarySide from './components/SummarySide';

interface Props {

}


const Summary : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('summary');
    const [priceOn, setPriceOn] = useState(false);
    const [isValidate, setIsValidate] = useState(false);
    const [showExportModal, setShowExportModal] = useState(false);

    const history = useHistory();

    // HOOKS

    // METHODS
    const modify = () => {
        history.push(routes.lensConsultation.path);
    }

    const validate = () => {
        setIsValidate(!isValidate);
    }

    const exportSummary = () => {
        setShowExportModal(true);
    }

    return (
        <div className={B() + ' page'}>    
            <SummarySide priceOn={priceOn}/>
            <div className={E('main')}>
                <div className={E('buttons')}>
                    <Button 
                        weight="normal"
                        text={`Price ${ !priceOn ? 'OFF' : 'ON'} `}
                        type={ !priceOn ? 'secondary' : 'primary'} 
                         
                        click={() => setPriceOn(!priceOn)}/>

                    {
                        !isValidate && 
                        <>
                            <Button weight="normal" text="Modify" type="secondary"  icon="x" click={modify}/>
                            <Button weight="normal" text="Validate" type="secondary"  icon="check" click={validate}/>
                        </>
                    }

                    {
                        isValidate && 
                        <>
                            <Button type="secondary"  icon="x" click={validate}/>
                            <Button 
                                weight="normal"
                                className="export"
                                text="Export" 
                                type="secondary" 
                                 
                                icon="export" 
                                click={exportSummary}/>
                        </>
                    }
                </div>
                
                <div className={E('wrapper')}>
                    <SummaryDetails data={summaryData} title="CUSTOMIZED OFFER" pair={1}/>
                    <SummaryDetails data={summaryData} pair={2}/>

                    <div className={E('notes')}>
                        <label>Notes</label>
                        <textarea></textarea>
                    </div>

                    <div className={E('footer')}>
                        <Icon name="qrcode-sample" type="image"/>
                        <Icon name="essilor-logo" type="image"/>
                    </div>
                </div>
                <br/>
            </div>

            <Modal show={showExportModal} width="62rem" onClose={() => setShowExportModal(false)}>
                <div className={E('export-modal')}>
                    <h2>Export</h2>
                    <div className="buttons">
                        <Button weight="normal" text="QR Code" type="secondary"  icon="qrcode"/>
                        <Button weight="normal" text="PMS" type="secondary"  icon="monitor-blue"/>
                        <Button weight="normal" text="Print" type="secondary"  icon="print"/>
                        <Button weight="normal" text="Mail" type="secondary"  icon="mail"/>
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default Summary;