
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ButtonGroup from '../../components/ui/ButtonGroup';
import DEMO_CONTROL_BUTTONS from '../../constants/demonstrations.contants';
import productDemoSlice, { getProductDemo } from '../../store/slices/products-demo.slice';
import productsSlice, { getProducts } from '../../store/slices/products.slice';
import LensFamilyType from '../../types/lens-family.type';
import ProductDemoType from '../../types/product-demo.type';
import useBEM from '../../utils/hooks/useBEM';
import ProductDemo from './components/ProductDemo';
import ProductDemoCompare from './components/ProductDemoCompare';
import ProductDemoSelection from './components/ProductDemoSelection';
import ProductDescription from './components/ProductDescription';

interface Props {
    type?: 'page' | 'modal'
}

const Demonstrations : React.FC<Props>  = ({
    type = 'page'
}) =>  {
    
    // VARIABLES
    const [B, E] = useBEM('demonstrations');
    const dispatch = useDispatch();
    const { brandToDemo, productToDemo } =  useSelector(getProducts);
    const { 
        demoType, 
        isShowProductDescription, 
        isControlsDisabled,
        isCompareEnabled,
        lensFamily
    } = useSelector(getProductDemo);
    const [position, setPosition] = useState('center');    

    // HOOKS
    useEffect(() => {
        return () => {
            changeDemoType(ProductDemoType.DRIVE);
        }
    }, [])

    // METHODS
    const changeDemoType = (type: ProductDemoType) => {
        dispatch(productDemoSlice.actions.changeDemoType(type))
    }

    const hideDescription = () => {
        dispatch(productDemoSlice.actions.showProductDescription(false))
    }

    const toggleCompare = () => {
        dispatch(productDemoSlice.actions.enableCompare(!isCompareEnabled));
    }

    const setLensType = (value: LensFamilyType) => {
        dispatch(productDemoSlice.actions.selectLensFamily(value))
    }

    const onSelectProduct = (product: any) => {
        dispatch(productsSlice.actions.selectProductTodemo(product));
    }

    return (
        <div className={B(type)}>
            
            <div className={E('canvas')}>
                {
                    !isCompareEnabled && 
                    <>                          
                        <ProductDemoSelection 
                            onSelectProduct={(p) => onSelectProduct(p)}
                            selectedProduct={productToDemo}
                            products={brandToDemo.products}
                        />
                        <ProductDescription isOpen={isShowProductDescription} onClose={() => hideDescription()}/>  
                        <ProductDemo />   
                    </>
                }
                
                { isCompareEnabled && <ProductDemoCompare/>}
            </div>


            <div className={E('controls')}>
                <ButtonGroup 
                    value={demoType}
                    onSelect={v => changeDemoType(v as ProductDemoType)}
                    buttons={DEMO_CONTROL_BUTTONS.demoTypes}/>


                {
                    brandToDemo.id === 'varilux' &&
                    <ButtonGroup 
                        value={position}
                        onSelect={v => setPosition(v)}
                        buttons={DEMO_CONTROL_BUTTONS.position}/>
                }

                {
                    brandToDemo.id !== 'varilux' &&
                    <ButtonGroup 
                        value={'center'}
                        buttons={[DEMO_CONTROL_BUTTONS.position[0]]}/>
                }

                

                <ButtonGroup 
                    value={isCompareEnabled ? 'compare' : ''}
                    onSelect={v => toggleCompare()}
                    buttons={DEMO_CONTROL_BUTTONS.compare}/>

                <ButtonGroup 
                    value={lensFamily}
                    onSelect={v => setLensType(v as LensFamilyType)}
                    buttons={DEMO_CONTROL_BUTTONS.lensTypes}/>

                {
                    (isControlsDisabled || isCompareEnabled) && 
                    <div className={E('controls-disabled')}></div>
                }
            </div>            
        </div>
    )
}

export default Demonstrations;
