import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import productsSlice, { getProducts } from '../../../store/slices/products.slice';
import useBEM from '../../../utils/hooks/useBEM';
import Icon from '../../ui/Icon';
import SubProducts from './SubProducts';

interface Props {
    id: string,
    label: string,
    background: string,
    logo?: string,
    subProducts: any[]
}

const ProductCard : React.FC<Props>  = ({
    id,
    label,
    background,
    logo,
    subProducts,
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('product-card');
    const dispatch = useDispatch();
    const  { brandToDemo } = useSelector(getProducts);

    // HOOKS

    // METHODS
    const selectBrand = () => {
        dispatch(productsSlice.actions.selectBrandTodemo({
            id,
            label,
            products: subProducts
        }))
    }

    return (
        <li className={B()}>                                        
            <Icon
                className={E('background')}
                name={background} 
                type="image" 
                background={true}/>

            {
                logo && 
                <Icon 
                    className={E('logo')}
                    name={logo} 
                />
            }
            
            
            <div className={E('see-more')} onClick={selectBrand}>
                See more <Icon name="eye" type="image"/>
            </div>     

            <SubProducts 
                products={subProducts}
                title={label} 
                open={brandToDemo && brandToDemo.id === id}
            />                                   
        </li>
    )
}

export default ProductCard;