import React, { useState } from 'react';
import Icon from '../../components/ui/Icon';
import Button from '../../components/ui/Button';
import StartFrom from './components/StartFrom';
import AgeCategory from './components/AgeCategory';
import ContinueWithID from './components/ContinueWithID';
import Modal from '../../components/Modal';
import InputText from '../../components/ui/InputText';
import { useSelector } from 'react-redux';
import { getUser } from '../../store/slices/user.slice';
import useBEM from '../../utils/hooks/useBEM';
import settings from '../../constants/settings.constants';


interface Props {

}

const HomePage : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('home');
    const [isStart, setStart] = useState(false);
    const [showStartFromIDModal, setShowStartFromIDModal] = useState(false);
    const [startFromIDField, setStartFromIDField] = useState<string>();

    const { id } = useSelector(getUser);

    const cards = [
        { icon: 'mic', label: 'What\'s new' },
        { icon: 'emat', label: 'eMat' },
        { icon: 'catalog', label: 'My Catalog' }
    ];

    // HOOKS

    // METHODS
    const onSelectStart = (type: string) => {
        setShowStartFromIDModal(type==='id');
    }

    const modalOnClose = () => {
        // setId('');
        setShowStartFromIDModal(false);
    }

    const idOnchange = (event: any) => {
        setStartFromIDField(event.target.value);
    }

    return (
        <div className={B() + ' page'}>
            {cards.map((card,index) => (
                <div className={E('card')} key={index}>                
                    <Icon name={card.icon}/>
                    <h2>{card.label}</h2>
                </div>
            ))}

            <div className={E('main')}>
                <div className={E('content')}>
                    <Icon className={E('eyeglass')} name="eyeglass"/>
                    <h1 className={E('banner')}>
                        Lens Proposal Builder
                    </h1>
                    
                    {   
                        !isStart && !id &&
                        <Button 
                            click={() => setStart(true)}
                            icon="right-arrow-circle"
                            text="Start" 
                            type="secondary"
                            outlined={false}
                        />
                    }
                </div>

                { id && <ContinueWithID/> }
                
                { !isStart ?
                    <StartFrom onSelect={(type) => onSelectStart(type)}/> :
                    <AgeCategory onClose={() => setStart(false)}/>
                }
            </div>

            <Modal show={showStartFromIDModal} onClose={() => modalOnClose()}>
                <div className={E('start-from-id-popup')}>
                    <h2>Start from an ID</h2>
                    
                    <div>
                        <InputText 
                            value={id}
                            placeholder="Enter an ID" 
                            maxlength={6} 
                            onChange={idOnchange}/>
                        
                        { startFromIDField && <Button text="Next" icon="right-arrow-circle-white"/> }
                        
                    </div>
                </div>
            </Modal>

            {/* NOTE: Create utility function to get environment settings */}
            <label className={E('version')}>
                {settings.dev.version}
            </label>
        </div>
    )
}

export default HomePage;