import React from 'react';
import useBEM from '../../../utils/hooks/useBEM';
import videoDemo from '../../../assets/videos/video-demo-temp.mp4';
import Icon from '../../../components/ui/Icon';
import ProductDemoType from '../../../types/product-demo.type';
import { useSelector } from 'react-redux';
import ReactPannellum from "react-pannellum";
import sphereImage from '../../../assets/images/sphere-image.jpg';
import { getProductDemo } from '../../../store/slices/products-demo.slice';




interface Props {

}

const ProductDemo : React.FC<Props>  = () =>  {
    // VARIABLES
    const  { demoType } = useSelector(getProductDemo);
    const [B, E] = useBEM('product-demo');
    const style = {
        width: "100%",
        height: "100%",
        background: "#000000"
    }

    const config = {
        // autoRotate: -2,
        autoLoad: true,
        showZoomCtrl: false,
        showFullscreenCtrl: false,
    };

    // HOOKS

    // METHODS

    return (
        <div className={B()}>
            
            {ProductDemoType.SPHERE_IMAGE === demoType && 
                <ReactPannellum
                    id={"id"}
                    sceneId={"sceneId"}
                    imageSource={sphereImage}
                    config={config}
                    style={style}/>
            }
            

            { ProductDemoType.DRIVE === demoType && 
                <Icon name="demonstration-car" type="image" background={true} className={E('drive')}/>               
            }

            { ProductDemoType.VIDEO === demoType && 
                <video 
                    className={E('video')}
                    playsInline
                    autoPlay={true} loop={true}  muted>
                    <source src={videoDemo} type="video/mp4"/> 
                    Your browser does not support the video tag.
                </video>
            }

        </div>
    )
}

export default ProductDemo;